
*{box-sizing:border-box
}
a{text-decoration:none
}
html{font-family:"Helvetica Neue", Helvetica, Arial, sans-serif
}
body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,input,button,textarea,p,blockquote,th,td{margin:0;padding:0
}
h1,h2,h3,h4,h5,h6{font-weight:500;line-height:1.426
}
article h1,article h2,article h3,article h4,article h5,article h6{margin:10px 0 15px 0
}
article h1,section h1{font-size:2em
}
a,a:hover,a:active,a:focus,input,input:hover,input:active,input:focus,select,select:hover,select:active,select:focus,textarea,textarea:hover,textarea:active,textarea:focus{outline:none
}
button,button:active,button:focus,button:hover{outline:none
}
ul,ol{list-style:none
}
body{background-color:#f4f4f4
}
.container{display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-direction:column;flex-direction:column;-ms-flex-wrap:wrap;flex-wrap:wrap;padding:30px 80px
}
@media (max-width: 920px){
.container{padding:20px 30px
}
}
@media (max-width: 530px){
.container{padding:10px 0
}
}
.container .list{display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;margin:0 auto
}
@media (min-width: 2059px){
.container .list{width:auto;max-width:auto;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center
}
}
@media (max-width: 2059px){
.container .list{max-width:1750px
}
}
@media (max-width: 1927px){
.container .list{max-width:1500px
}
}
@media (max-width: 1677px){
.container .list{max-width:1250px
}
}
@media (max-width: 1427px){
.container .list{max-width:1000px
}
}
@media (max-width: 1177px){
.container .list{max-width:750px
}
}
@media (max-width: 920px){
.container .list{max-width:500px
}
}
.container .list li{margin-bottom:15px;width:220px;margin:0 15px 15px 15px
}
@media (max-width: 530px){
.container .list li{margin:5px;width:calc(50% - 10px)
}
.container .list li .tools-group,.container .list li .attribution{display:none
}
}
.container .list li:hover .badge{opacity:1
}
.container .card{box-shadow:0 0 1px 0px rgba(0,0,0,0.16);width:100%;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;background-color:#fffffb;padding:8px
}
.main{position:relative;cursor:pointer;height:0;overflow:hidden;padding-bottom:75%
}
.main img{width:100%
}
.main .info{position:absolute;left:0;top:0;width:100%;height:100%;padding:15px;opacity:0;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;background-color:rgba(255,255,255,0.96);transition:all 0.25s
}
.main .info strong{font-weight:500;font-size:14px;line-height:1.2;color:#444;white-space:nowrap;text-overflow:ellipsis;overflow:hidden
}
.main .info small{margin-top:10px;font-size:12px;text-overflow:ellipsis;display:-webkit-box;-webkit-box-orient:vertical;-webkit-line-clamp:5;overflow:hidden;line-height:1.4;max-height:7;color:#777
}
.main .info em{position:absolute;bottom:10px;left:15px;margin-top:3px;font-size:12px;font-weight:normal;font-style:normal;line-height:1;color:#777;-ms-flex-negative:0;flex-shrink:0
}
.main:hover .info{opacity:1
}
.tools-group{display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-pack:end;justify-content:flex-end;font-size:12px;padding:8px 0 0 0;color:#aaa;position:relative
}
.tools-group span{background-position:0 1px;background-repeat:no-repeat;margin-left:10px
}
.tools-group .attachments{position:absolute;left:0;margin:-2px 0 0 0;background-size:contain;width:16px;height:16px;background-image:url("data:image/png;base64,")
}
.tools-group .view{padding-left:18px;background-image:url("data:image/png;base64,")
}
.tools-group .cmnt{padding-left:14px;background-image:url("data:image/png;base64,")
}
.tools-group .fav{padding-left:14px;background-image:url("data:image/png;base64,")
}
.attribution{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;font-size:14px;padding:10px 5px;color:#aaa
}
.attribution img{width:16px;height:16px;border-radius:50%
}
.attribution a{color:#3498db;margin:0 3px
}
.attribution .badge{padding:1px 3px;font-family:Helvetica;font-size:10px;font-weight:bold;line-height:1;text-transform:uppercase;color:#fffffb;background:#ccc;border-radius:3px;opacity:0;transition:all .2s
}
.attribution .badge:hover{background-color:#77d5d4
}
.attribution .badge.team-badge:hover{background-color:#3498db
}
.loading-more{color:#777;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center
}
.infinite{text-align:center;margin:20px
}
.infinite a.sign-up-a{display:block;width:400px;margin:0 auto;padding:15px 12px;font-size:14px;font-weight:500;color:#fffffb;border-radius:4px;background-image:linear-gradient(0deg, #ea4c89, #ff72a9)
}
.infinite a.sign-up-a:hover{background-image:linear-gradient(0deg, #e94383, #ff68a3)
}
.infinite a.sign-up-a:active{box-shadow:inset 0 2px 3px 0 rgba(0,0,0,0.36)
}
.infinite a.sign-in-a{color:#3498db;margin-top:20px;display:block
}
.infinite a.sign-in-a:hover{color:#124364
}



